body {
  margin: 0px;
}


.Container {
    padding-left : 15px;
    padding-right: 15px;
    margin-top: 60px;
    padding-top: 5px;
    font-weight : bold;
    font-family: 'Quicksand', sans-serif;
    color : #494444;
}

#dot {
    position: fixed;
    -webkit-animation-duration: 7s;
            animation-duration: 7s;     
    -webkit-animation-iteration-count: infinite;     
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: cubic-bezier(0.47, 0, 0.745, 0.715);
            animation-timing-function: cubic-bezier(0.47, 0, 0.745, 0.715);
    z-index: -1;
    color: #0676FC;
    left: -10px;
    top: -10px;
}

@-webkit-keyframes mv1 {
    from {top: -5%;left : 10%;}
    to {top: 100%;left : 80%;}
}

@keyframes mv1 {
    from {top: -5%;left : 10%;}
    to {top: 100%;left : 80%;}
}

@-webkit-keyframes mv2 {
    from {top: 10%;left : -3%;}
    to {top: 95%;left : 100%;}
}

@keyframes mv2 {
    from {top: 10%;left : -3%;}
    to {top: 95%;left : 100%;}
}

@-webkit-keyframes mv3 {
    from {top: -10%;left : -3%;}
    to {top: 80%;left : 100%;}
}

@keyframes mv3 {
    from {top: -10%;left : -3%;}
    to {top: 80%;left : 100%;}
}

@-webkit-keyframes mv4 {
    from {top: -3%;left : -3%;}
    to {top: 100%;left : 90%;}
}

@keyframes mv4 {
    from {top: -3%;left : -3%;}
    to {top: 100%;left : 90%;}
}
